import React, {useState, useEffect}  from 'react';
import { useHistory, useParams } from 'react-router-dom';

import styled from 'styled-components';

import './Detail.scss';

let MBox = styled.div`
    padding: 20px;
`; 
let MTitle = styled.h4`
    font-size : 25px;
    color : ${ props => props.color };
`;
function Detail(props){

    let [alert, alertMod] = useState(true);

    useEffect(() => {
        let timer = setTimeout(() => {
            alertMod(false);
            console.log("어라~ ");
        }, 2000);

        return () => {clearTimeout(timer)};
    }, []);

    function remainMod(){
        let remain = [...props.remain];
        remain[id] = props.remain[id] - 1;
        props.remainMod(remain)
    };

    let [inputData,inputDateMod] = useState('');
    let {id} = useParams();
    let history = useHistory();
    let findShoes = props.shoes.find((item)=>{
        return item.id = id 
    })  
    return(
        <div className="container">
            <MBox>
                <MTitle color="red">Detail</MTitle>
            </MBox>
            {inputData}
            <input onChange={(e)=>{inputDateMod(e.target.value)}}></input>
            {alert ? (
            <div className="my-alert">
                <p>재고가 얼마 남지 않았어요~ </p>
            </div>
            ) : null
            }
        <div className="row">
          <div className="col-md-6">
            <img src={`https://codingapple1.github.io/shop/shoes${Number(id) + 1}.jpg`} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5"> {findShoes.title} </h4>
            <p>{findShoes.content}</p>
            <p>{findShoes.price}</p>

            <RemainInfo remain={props.remain} id={id}></RemainInfo>

            <button className="btn btn-danger" onClick={()=>{remainMod()}}>주문하기</button> {' '}
            <button className="btn btn-danger" onClick={()=>{history.goBack()}}>뒤로가기</button> 
          </div>
        </div>
        </div> 
    );
}

function RemainInfo(props){
    return(
        <p>재고 : {props.remain[props.id]}</p>
    );
}
export default Detail;